@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;800&display=swap);
body,html{margin:0;font-family:"Poppins", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.slide-in-menu{display:flex;flex-direction:column;width:100vw;height:100vh}.slide-in-menu .slide-in-nav{background-color:#28292b;display:flex;flex-direction:column;justify-content:space-between;align-items:baseline;letter-spacing:0.1em;text-transform:uppercase;font-weight:500;width:80vw;height:100vh;padding:20px 0}.slide-in-menu .slide-in-nav .slide-in-nav-group{display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-start;padding:10px}

.burger-container{display:inline-block;cursor:pointer;position:relative;top:15px;right:25px}.burger-container .bar1,.burger-container .bar2,.burger-container .bar3{width:34px;height:4px;background-color:#fff;margin:7px 0;transition:0.4s}.burger-container .change.bar1{transform:rotate(-45deg) translate(-9px, 6px)}.burger-container .change.bar2{opacity:0}.burger-container .change.bar3{transform:rotate(45deg) translate(-8px, -8px)}

.ingredient-input-group{display:flex;flex-direction:row;justify-content:center;position:relative;width:100%}.ingredient-input-group .button-group{display:flex;position:absolute;right:14%;top:10%}.ingredient-input-group .button-group .ingredient-button{background-color:transparent;border-radius:0.5rem;border:none;color:#b3b4b6;cursor:pointer;display:flex;flex-direction:row;font-size:20px;font-weight:lighter;height:2rem;justify-content:center;width:2rem;margin:0 2px}.ingredient-input-group .button-group .ingredient-button:hover{background-color:#28292b}.ingredient-input-group .button-group .add{position:relative;color:#b3b4b6;border:none;transform:rotate(45deg);font-size:18px;font-weight:100;background-color:transparent}.ingredient-input-group .button-group .delete{position:relative;top:3px}

.ingredient-list{display:flex;flex-direction:column;justify-content:space-between;align-content:space-between;padding:0px;width:100%}.ingredient-list .ingredient{display:flex;justify-content:space-between;align-items:center;border-bottom:1px solid #28292b;border-top:1px solid #28292b;padding:2px 0;width:100%}.ingredient-list .ingredient:last-child{border-bottom:none}.ingredient-list .ingredient .ingredient-button{background-color:transparent;border-radius:0.5rem;border:none;color:#b3b4b6;cursor:pointer;display:flex;flex-direction:row;font-size:20px;font-weight:lighter;transition:0.4s;justify-content:center}.ingredient-list .ingredient .ingredient-button:hover{background-color:#28292b}

.card{align-items:center;color:#fff;background-color:#202124;border:1.5px solid #404143;border-radius:6px;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.4);display:flex;flex-direction:column;justify-content:center;margin:10px 0;width:100%;max-width:500px;overflow:hidden}.card .card-header{display:flex;width:90%;max-width:500px;justify-content:space-between;align-items:center;padding:0;margin:10px 0;color:#fff}.card .card-header .card-title{margin:0;font-weight:600;letter-spacing:0.07em;text-transform:uppercase}@media screen and (max-width: 450px){.card .card-header .card-title{padding:0 0 0 5px}}.card .card-header .title-link{font-size:20px;border-radius:0.5rem;text-decoration:none;color:#b3b4b6;cursor:pointer;border:none;background-color:transparent;transition:all 0.5s;padding:0 5px}.card .card-header .title-link:hover{background-color:#28292b;transform:scale(1.1)}.card .card-header .card-header-buttons{display:flex;flex-wrap:nowrap;position:relative;top:2px}.card .card-body{color:#fff;display:flex;flex-direction:column;width:90%;align-items:center;justify-content:space-between;text-transform:capitalize}

.custom-button{width:100%;cursor:pointer;background-color:#28292b;color:#fff;border:none;border-radius:4px;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.2);text-align:center;padding:20px 0;margin:10px 0;font-size:1rem;transition:0.3s}.custom-button:hover{background-color:#404143}.custom-button.google-sign-in{background-color:#4285f4;color:#fff}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}

.card-list{width:95%}.card-list .masonry-grid{display:flex;margin-left:-10px;width:100%}.card-list .masonry-grid .masonry-grid_column{display:flex;flex-direction:column;align-items:center;padding-left:20px;background-clip:padding-box}

.group{display:flex;flex-direction:column;align-self:center;position:relative;margin:0;width:70%}.group .form-input{background:none;background-color:#202124;color:#fff;font-size:18px;line-height:1;display:block;width:100%;border:none;padding-left:5px;border-radius:0;border-bottom:1px solid #28292b;margin:15px 0}.group .form-input:focus{outline:none;background-color:#202124}.group .form-input:focus ~ .form-input-label{top:-5px;font-size:12px;color:#404143}.group .form-input-label{color:#b3b4b6;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:15px;transition:300ms ease all}.group .form-input-label.shrink{top:-5px;font-size:12px;color:#404143}

.searchbox{display:flex;justify-content:center;padding:9px 0 0 0;width:100%;background-color:#202124;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.2);border-radius:4px}

.sign-in{align-items:center;background-color:#202124;color:#b3b4b6;display:flex;flex-direction:column;justify-content:center;text-align:center;margin:20px;width:40%}@media screen and (max-width: 750px){.sign-in{width:90%}}.sign-in h2{color:#fff;font-weight:600;letter-spacing:0.07em;text-transform:uppercase}.sign-in .sign-in-form{display:flex;flex-direction:column;justify-content:center;margin:20px;width:90%}.sign-in .sign-in-form .button-container{display:flex;flex-wrap:wrap;width:100%}.sign-in .error{color:red;margin:0 0 10px;text-align:center}

.sign-in-and-sign-up-page{display:flex;width:100%;justify-content:center;margin:30px auto}@media screen and (max-width: 750px){.sign-in-and-sign-up-page{flex-direction:column;align-items:center}}

.footer{background-color:#202124;height:80px;display:flex;justify-content:space-evenly;align-items:center;width:100%;color:#fff;text-align:center;margin-top:30px}.footer .social-section{align-items:stretch;display:flex;justify-content:space-evenly;list-style-type:none;text-align:center;padding:0;margin:0}.footer .social-section .social{width:40px}.footer .social>a>img{border-radius:50%;transition:all, 0.5s;border:none;object-fit:cover;width:100%}.footer .social>a>img:hover{background-color:#b3b4b6;transform:scale(1.2)}

.app{background-image:url(/PANTRI/static/media/background-dark.654cf2d9.png);background-color:#202124;background-repeat:repeat;background-size:30vmin;display:flex;flex-direction:column;justify-content:space-between;min-height:100vh}.app .app-header{background-color:#202124;min-height:80px;padding:10px 0;width:100%}.app .app-header .nav{align-items:center;display:flex;flex-wrap:nowrap;justify-content:space-evenly}.app .app-header .nav .logo{max-width:300px;width:30%}.app .app-header .nav .nav-group{display:flex;flex-wrap:wrap;justify-content:center}.app .app-header .small-screen{display:flex;flex-wrap:nowrap;justify-content:space-between;width:100%}.app .app-header .small-screen .logo{height:auto;margin:0 10px;max-width:200px;width:60%}.nav-link{color:#fff;font-weight:600;letter-spacing:0.1em;margin:0 10px;padding:0.5rem;text-align:center;text-decoration:none;text-transform:uppercase;transition:all 0.5s;white-space:nowrap;cursor:pointer}.nav-link:hover{color:rgba(255,255,255,0.5);transform:scale(1.15)}a{color:#fff;text-decoration:none;transition:all 0.5s}a:hover{color:rgba(255,255,255,0.5);transform:scale(1.15)}.logo{width:90%}*{box-sizing:border-box}.body{flex-grow:1}.fade-in{animation:fadeIn ease 0.8s;-webkit-animation:fadeIn ease 0.8s;-moz-animation:fadeIn ease 0.8s;-o-animation:fadeIn ease 0.8s;-ms-animation:fadeIn ease 0.8s}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.page{align-items:center;display:flex;flex-direction:column;justify-content:center;width:100%}.page .page-header{max-width:500px;width:100%}.page .recipes-empty{align-items:center;background-color:#202124;border-radius:4px;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.2);display:flex;flex-direction:column;margin:20px 20px}.page .title{align-items:center;background-color:#202124;color:#fff;border-radius:4px;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.2);display:flex;font-weight:600;letter-spacing:0.07em;text-transform:uppercase;justify-content:center;padding:15px 0;text-align:center;width:100%}.page .new-recipe-title{font-weight:600;letter-spacing:0.07em;text-transform:uppercase}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,textarea:-webkit-autofill,textarea:-webkit-autofill:hover,textarea:-webkit-autofill:focus,select:-webkit-autofill,select:-webkit-autofill:hover,select:-webkit-autofill:focus{border:none;-webkit-text-fill-color:#fff;-webkit-box-shadow:0 0 0px 1000px #202124 inset;-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s}

